
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 0px
}

ul[data-v-faec2cd6]{
  border:none;
}
.div1[data-v-faec2cd6]{
   margin:0 auto;
    width:500px
}
li[data-v-faec2cd6]{
  height:40px;
  line-height: 60px;
  border-bottom: 1px solid #ddd;
}
input[data-v-faec2cd6]{
text-align:center;
font-size:1.4em;
height:2.3em;
border-radius:8px;
border:1px solid #c8cccf;
color:#6a6f77;
 -moz-appearance: none;
 display:block;
 outline:0;
 width:100%;
}
.td1[data-v-faec2cd6]{
  visibility:hidden
}
button[data-v-faec2cd6] {
    background-color: #4CAF50; /* Green */
    border: none;
    color: white;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 16px 2px;
    cursor: pointer;
    -webkit-transition-duration: 0.4s; /* Safari */
    transition-duration: 0.4s;
    border-radius: 8px;
}
button[data-v-faec2cd6]:hover {
    -webkit-box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24),0 17px 50px 0 rgba(0,0,0,0.19);
            box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24),0 17px 50px 0 rgba(0,0,0,0.19);
}
.tbl[data-v-faec2cd6]{border:1px solid lightgray;
}






















